import connect from "connect";
import { blue, green } from "picocolors";
import { optimize } from "../optimizer/index";
import { resolvePlugins } from "../plugins/index";
import { PluginContainer, createPluginContainer } from "../pluginContainer";
import { Plugin } from "../plugin";
import { indexHtmlMiddleware } from "./middlewares/indexHtml";
import { transformMiddleware } from "./middlewares/transform";
import { staticMiddleware } from "./middlewares/static";
import { normalizePath } from "../utils";
import { ModuleGraph } from "../ModuleGraph";
import chokidar,{FSWatcher} from 'chokidar'
import { createWebSocketServer } from '../ws';
import { bindingHMREvents } from "../hmr";

export interface ServerContext {
  root: string;
  pluginContainer: PluginContainer;
  app: connect.Server;
  plugins: Plugin[];
  moduleGraph:ModuleGraph;
  ws:{send:(data:any)=>void;close:()=>void};
  watcher:FSWatcher
}

export async function startDevServer() {
  const app = connect();
  const root = process.cwd();
  
  const startTime = Date.now();
  const plugins = resolvePlugins();
  //ÂàõÂª∫Êñá‰ª∂ÁõëÂê¨Âô®
  const watcher=chokidar.watch(root,{
    ignored:["**/node_modules/**","**/.git/**"],
    ignoreInitial:true
  })
  //ÂàõÂª∫Êèí‰ª∂ÂÆπÂô®
  const moduleGraph=new ModuleGraph((url)=>pluginContainer.resolveId(url))
  const pluginContainer = createPluginContainer(plugins);

  //WebSocketÂØπË±°
  const ws=createWebSocketServer(app)

  //ÂàõÂª∫configureServerÊâÄÈúÄÁöÑserverÂÆû‰æã
  const serverContext: ServerContext = {
    root: normalizePath(process.cwd()),
    app,
    pluginContainer,
    plugins,
    moduleGraph,
    ws,
    watcher
  };
  bindingHMREvents(serverContext)

  for (const plugin of plugins) {
    if (plugin.configureServer) {
      //viteÁöÑconfigureServer‰∏≠Èó¥‰ª∂ÔºåÁî®Êù•ÊãìÂ±ïviteÁöÑdevServer
      //Áî®Êù•Â¢ûÂä†Ëá™ÂÆö‰πâ‰∏≠Èó¥‰ª∂
      await plugin.configureServer(serverContext);
    }
  }
  
  app.use(transformMiddleware(serverContext));
  //Â§ÑÁêÜhtmlËµÑÊ∫êÁöÑ‰∏≠Èó¥‰ª∂
  app.use(indexHtmlMiddleware(serverContext));
  app.use(staticMiddleware(serverContext.root));

  app.listen(3000, async () => {
    //Á≠âÂæÖ‰æùËµñÈ¢ÑÊûÑÂª∫
    await optimize(root);
    console.log(green("üöÄ xpÁöÑNo-BundleÊúçÂä°Â∑≤ÁªèÂêØÂä®Âï¶!"), `ËÄóÊó∂Ôºö${Date.now() - startTime}ms`);
    console.log(`>Êú¨Âú∞ËÆøÈóÆË∑ØÂæÑÔºö${blue("http://localhost:3000")}`);
  });
}
