//ÂÆûÁé∞Ê®°Âùó‰æùËµñÂõæ
//‰∏∫‰∫ÜÊñπ‰æøÁÆ°ÁêÜÂêÑ‰∏™Ê®°Âùó‰πãÈó¥ÁöÑ‰æùËµñÂÖ≥Á≥ªÔºü
import { PartialResolvedId, TransformResult } from "rollup";

import { cleanUrl } from "./utils";

export class ModuleNode {
  //ËµÑÊ∫êËÆøÈóÆurl
  url: string;
  //ËµÑÊ∫êÁªùÂØπË∑ØÂæÑ
  id: string | null = null;
  importers = new Set<ModuleNode>();
  importedModules = new Set<ModuleNode>();
  transformResult: TransformResult | null = null;
  lastHMRTimestamp = 0;
  constructor(url: string) {
    this.url = url;
  }
}

export class ModuleGraph {
  //ËµÑÊ∫êurlÂà∞ModuleNodeÁöÑÊò†Â∞ÑË°®
  urlToModuleMap = new Map<string, ModuleNode>();
  //ËµÑÊ∫êÁªùÂØπË∑ØÂæÑÂà∞ModuleNodeÁöÑÊò†Â∞ÑË°®
  idToModuleMap = new Map<string, ModuleNode>();

  constructor(private resolveId: (url: string) => Promise<PartialResolvedId | null>) {}

  getModuleById(id: string): ModuleNode | undefined {
    return this.idToModuleMap.get(id);
  }

  async getModuleByUrl(rawUrl: string): Promise<ModuleNode | undefined> {
    const { url } = await this._resolve(rawUrl);
    return this.urlToModuleMap.get(url);
  }

  async ensureEntryFromUrl(rawUrl: string): Promise<ModuleNode> {
    const { url, resolvedId } = await this._resolve(rawUrl);
    //È¶ñÂÖàÊ£ÄÊü•ÁºìÂ≠ò
    if (this.urlToModuleMap.has(url)) {
      return this.urlToModuleMap.get(url) as ModuleNode;
    }
    //Ëã•Êó†ÁºìÂ≠òÔºåÊõ¥Êñ∞ urlModuleMap Âíå idToModuleMap
    const mod = new ModuleNode(url);
    module.id = resolvedId;
    this.urlToModuleMap.set(url, mod);
    this.idToModuleMap.set(resolvedId, mod);
    return mod;
  }

  async updateModuleInfo(mod: ModuleNode, importedModules: Set<string | ModuleNode>) {
    const prevImports = mod.importedModules;
    for (const curImports of importedModules) {
      const dep =
        typeof curImports === "string"
          ? await this.ensureEntryFromUrl(cleanUrl(curImports))
          : curImports;
      if (dep) {
        mod.importedModules.add(dep);
        dep.importers.add(mod);
      }
    }
    //Ê∏ÖÈô§Â∑≤Áªè‰∏çË¢´ÂºïÁî®ÁöÑ‰æùËµñ
    for (const prevImport of prevImports) {
      if (!importedModules.has(prevImport.url)) {
        prevImport.importers.delete(mod);
      }
    }
  }

  //HMRËß¶ÂèëÊó∂‰ºöÊâßË°åËøô‰∏™ÊñπÊ≥ï
  invalidateModule(file: string) {
    const mod = this.idToModuleMap.get(file);
    if (mod) {
      //Êõ¥Êñ∞Êó∂Èó¥Êà≥
      mod.lastHMRTimestamp = Date.now();
      mod.transformResult = null;
      mod.importers.forEach((importer) => {
        this.invalidateModule(importer.id!);
      });
    }
  }

  private async _resolve(url: string): Promise<{ url: string; resolvedId: string }> {
    const resolved = await this.resolveId(url);
    const resolvedId = resolved?.id || url;
    return { url, resolvedId };
  }
}
